import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";
function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">James Davis </span>
            from <span className="purple"> Canada.</span>
            <br />
            web Developer who loves to transform ideas into reality using code.
            <br />
            I 'm a web developer that has over 8 years experience in several web
            technologies such as PHP, React.JS, NodeJS, HTML, CSS and
            JavaScript. My main stacks are React.JS and Node.JS. Of course
            basicially I have enough knowledge about JavaScript, CSS and Html
            and than I think it is possible to develop web with only them. It
            means the basic knowledge is very important in everything. Also I
            have experience in managing database such as MySQL, Oracle and
            MongoDB. I have done several projects such as E-Shop, Payment,
            <br />
            <br />
            Apart from coding, some other activities that I love to do!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> Playing Vasketball
            </li>
            <li className="about-activity">
              <ImPointRight /> Listening Music
            </li>
            <li className="about-activity">
              <ImPointRight /> Walking
            </li>
          </ul>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
